.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
  }

  
  .user img{
      width: 48px;
      border-radius: 50%;
  }

  .articleCards{
    flex-wrap: wrap;
  }

  
  .articleCards .card .text{
    position: relative;
    max-height: 150px;
    overflow-y:auto;
  }
  .articleCards textarea{
    resize: none;
  }
  .articleCards .bottom-card, .product .bottom-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .dateTable input{
    width: 100px;
    color: rgb(100, 189, 10);
  }
  .isAvailable{
    height: 20px;
    display: block;
  }
  .isAvailable input{
    position: relative;
    left: 70%;
  }
.dateFormContainer,
.adminFormContainer,
.boutiqueForm,
.articleFormContainer{
  position: absolute;
  background-color: rgba(255, 255, 255, 0.692);
  backdrop-filter: blur(10px);
  width: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 40%!important;
  margin-top: 50px;
  padding: 50px;
  z-index: 5;
  box-shadow: 0px 0px 15px 11px rgba(0,0,0,0.66);
}
@media all and (max-width: 1220px) {
  .dateFormContainer,
.adminFormContainer,
.boutiqueForm,
.articleFormContainer{
    left: 30%!important;
  }
}
@media all and (max-width: 1050px) {
  .dateFormContainer,
.adminFormContainer,
.boutiqueForm,
.articleFormContainer{
    left: 20%!important;
  }
}
@media all and (max-width: 900px) {
  .dateFormContainer,
.adminFormContainer,
.boutiqueForm,
.articleFormContainer{
    left: 27%!important;
    width: 500px;
  }
}
@media all and (max-width: 700px) {
  .dateFormContainer,
.adminFormContainer,
.boutiqueForm,
.articleFormContainer{
    left: 5%!important;
    width: 400px;
  }
  
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}